<template>
	<view class="w-form">
		<view :style="formStyle" class="formStyle" :class="marginTop ? 'margin-top' : ''">
			<u-form
				:labelWidth="labelWidth"
				:labelStyle="labelStyle"
				:labelPosition="labelPosition"
				:model="tempData"
				:rules="rules"
				ref="formRef"
			>
				<u-form-item
					v-for="(item, index) in formInfo"
					:key="index"
					:required="item.required || false"
					:borderBottom="item.borderBottom || false"
					:label="item.label"
					:prop="item.model"
					:label-width="item.labelWidth"
				>
					<template v-if="item.type === `input`">
						<input
							:style="item.class"
							class="baseInput"
							:placeholder="item.placeholder"
							v-model="tempData[item.model]"
							name="input"
						/>
					</template>
					<template v-if="item.type === `textarea`">
						<textarea
							:style="item.class"
							class="baseTextarea"
							v-model="tempData[item.model]"
							:placeholder="item.placeholder"
						></textarea>
					</template>
					<template v-if="item.type === `text`">
						{{ tempData[item.model] }}
					</template>
					<template v-if="item.type === `switch`">
						<u-switch v-model="tempData[item.model]"></u-switch>
					</template>
					<template v-if="item.type === `slot`">
						<!-- #ifdef H5  -->
						<slot :name="item.model"></slot>
						<!-- #endif -->
						<!-- #ifdef MP -->
						<slot name="{{item.model}}"></slot>
						<!-- #endif -->
					</template>
				</u-form-item>
			</u-form>
		</view>
	</view>
</template>
<script>
export default {
	props: {
		formInfo: {
			type: Array,
			required: true,
			default: () => []
		},
		marginTop: {
			type: Boolean,
			default: false
		},
		formData: {
			type: Object,
			required: false
		},
		rules: {
			type: Object,
			required: false
		},
		labelPosition: {
			type: String,
			default: 'left'
		},
		labelStyle: {
			type: Object,
			default: () => ({ color: '#0078d4', fontSize: '28rpx' })
		},
		//表单样式
		formStyle: {
			type: Object,
			required: false,
			default: () => ({})
		},
		labelWidth: {
			type: String,
			default: '140rpx'
		}
	},
	data() {
		return {
			tempData: this.formData
		}
	},
	watch: {
		tempData: {
			handler(newValueData) {
				this.$emit('update:formData', newValueData)
			},
			deep: true,
			immediate: true
		},
		formData: {
			handler(newValueData) {
				this.tempData = newValueData
			},
			deep: true,
			immediate: true
		}
	},
	methods: {}
}
</script>
<style lang="scss" scoped>
.w-form {
	.baseInput {
		height: 60rpx;
		font-size: 28rpx;
		border-radius: 4rpx;
		width: 100%;
		background-color: #eeeeee;
		padding: 0 20rpx;
	}
	.baseTextarea {
		height: 140rpx;
		font-size: 28rpx;
		width: 100%;
		background-color: #eee;
		border-radius: 4rpx;
		padding: 8rpx;
	}
	.formStyle {
		background-color: #fff;
		padding: 0 30rpx;
	}
}
</style>
